@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Baloo+Bhai);
@import url(https://fonts.googleapis.com/css?family=Ubuntu);
.main-content {
  display: flex;
  background-color: #4B5F6B;
  min-height: 100vh;
  min-width: 100vw;
  color: #fff;
  justify-content: center;
  align-items: center;
  flex-direction: column; }

.card-content {
  border: none !important;
  border-top-left-radius: 15px !important;
  border-top-right-radius: 15px !important;
  width: 400px !important;
  background-color: #151f2a !important;
  font-family: 'Montserrat', sans-serif;
  margin: 0px !important; }
  .card-content .ant-card-head {
    color: white !important;
    text-align: center;
    background-color: #263147 !important; }
  .card-content .ant-card-body {
    background-color: #f5f5f5;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    height: 450px !important;
    overflow: visible; }
    .card-content .ant-card-body .ant-btn:nth-child(1) {
      border-radius: 20px !important;
      align-self: center; }
    .card-content .ant-card-body h3 {
      margin-top: 15px;
      font-family: 'Baloo Bhai', cursive;
      font-size: 24px;
      align-self: center;
      margin-bottom: 0 !important; }
    .card-content .ant-card-body span:nth-child(3) {
      align-self: center;
      font-weight: 800 !important;
      color: #a8a8a8 !important; }
    .card-content .ant-card-body .announce {
      display: flex;
      justify-content: space-between;
      margin-top: 20px !important;
      color: #a8a8a8 !important; }
      .card-content .ant-card-body .announce i {
        border: 1px solid !important;
        padding: 3px !important;
        min-width: 10px !important;
        min-height: 10px !important;
        border-radius: 15px !important; }
    .card-content .ant-card-body .poll {
      display: flex;
      font-weight: bold;
      flex-wrap: wrap;
      font-size: 16px;
      margin-bottom: 20px; }
      .card-content .ant-card-body .poll .ant-radio-checked .ant-radio-inner {
        border-color: #f96555 !important; }
      .card-content .ant-card-body .poll .ant-radio-wrapper:hover .ant-radio,
      .card-content .ant-card-body .poll .ant-radio:hover .ant-radio-inner,
      .card-content .ant-card-body .poll .ant-radio-input:focus + .ant-radio-inner {
        border-color: #f96555 !important; }
      .card-content .ant-card-body .poll .ant-radio-inner::after {
        background-color: #f96555 !important; }
      .card-content .ant-card-body .poll label.ant-radio-wrapper span:nth-child(2) {
        color: #000 !important;
        font-family: 'Ubuntu', sans-serif;
        font-size: 16px; }
      .card-content .ant-card-body .poll label.ant-radio-wrapper.ant-radio-wrapper-checked span:nth-child(2) {
        color: #f96555 !important;
        font-family: 'Ubuntu', sans-serif;
        font-size: 16px; }
      .card-content .ant-card-body .poll p {
        margin-left: auto !important;
        margin-bottom: 5px !important;
        font-family: 'Ubuntu', sans-serif;
        color: #a8a8a8; }
      .card-content .ant-card-body .poll .persentage {
        max-height: 10px;
        background-color: #a8a8a8;
        width: 100%; }
        .card-content .ant-card-body .poll .persentage-bar {
          background-color: #5a5a5a; }
        .card-content .ant-card-body .poll .persentage .bar-chose {
          background-color: #f96555; }

.submit-btn {
  background-color: #2bb900 !important;
  align-self: flex-end !important;
  border: none !important;
  font-size: 20px !important;
  border-top-left-radius: 0 !important;
  border-top-right-radius: 0 !important;
  margin: 0 !important; }

